body {
    font-family: Gilroy;
    font-size: 14px;
    font-weight: 400;
}

.container {
    padding: 0 40px;
    margin: 0 auto;
    max-width: 1440px;
}

.back {
    background-image: url(./assets/back.png);
    background-size: 100% 100%;
    width: 100%;
    background-repeat: no-repeat;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;

}

.headers {
    padding-top: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
}

.logo {
    color: #fff;
    font-weight: 500;
    font-size: 28px;
    z-index: 1;
}

.nav {
    display: flex;
    font-weight: 500;
    color: #fff;
    background-color: #FFFFFF1A;
    border: 1px #FFFFFF4D solid;
    border-radius: 30px;
    padding: 10px 28px;
    gap: 35px;
}

.nav__btn {
    color: #000;
    border-radius: 30px;
    background-color: #fff;
    padding: 13px 39px;
}

.burger {
  display: none;
  flex-direction: column;
  gap: 5px;
  cursor: pointer;
  z-index: 1;
}

.burger span {
  display: block;
  width: 25px;
  height: 3px;
  background-color: #fff;
}

@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
  .logo {
    font-size: 20px;
    z-index: 10;
  }
  .nav, .nav__btn {
      display: none;
  }

  .burger {
      display: flex;
      z-index: 10;
      color: #000;
  }


  .nav.active {
      display: flex;
      align-items: center;
      flex-direction: column;
      position: absolute;
      left: 0;
      width: 100%;
      background-color: #fff;
      color: #000;
      padding: 90px 20px 20px 20px;
      gap: 10px;
      font-size: 20px;
      z-index: 1;
      align-items: flex-end;
  }
    .nav {
        display: none;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: 0;
        border-radius: 0;
        width: 100%;
        background-color: #333;
        padding: 20px;
        gap: 10px;
        transform: translateY(-100%);
        transition: transform 0.3s ease-in-out;
    }

    .nav.active {
        transform: translateY(0);
    }
    .nav__item {
      font-weight: 500;
      font-size: 48px;
    }

  }



.main {
    padding-top: 350px;
}

.main__title {
    display: flex;
    justify-content: space-between;
    padding-bottom: 72px;
}

.main__title__h h1 {
    color: #fff;
    max-width: 860px;
    font-size: 7vw;
    background: url(./assets/sw1.png) no-repeat right 19vh top 2vw;
}

.main__title__h p {
    color: #fff;
    font-size: 16px;
}

.main__span {
    font-weight: 700;
}

.main__title__two {
    background-color: #fff;
    max-width: 358px;
    height: 310px;
    width: 100%;
    border-radius: 30px;
    text-align: center;

}
.main__title__btn {
    padding: 20px 100px;
    color: #fff;
    background-color: #2971C4;
    border-radius: 30px;
    font-weight: 600;
    font-size: 16px;
    margin-top: 30px;
    max-width: 100%;
}

.main__card__img {
    position: relative;
}


@media (max-width: 768px) {
  .main {
    padding-top: 50px;
  }
  .main__title {
    flex-direction: column;
    align-items: center;
    gap: 50px;
  }
}




.lenta {
    padding: 100px 0 50px 0;
    display: flex;
    overflow: hidden;

}
.l_i {
    color: #000!important;
}

.lebta__item {
    width: 100%;
    font-weight: 700;
    color: #1D1D1D1A;
    font-size: 20px;
}


@media (max-width: 768px) {
.lenta {
  padding: 50px 0;
}
.lebta__item {
  font-size: 14px;
  min-width: 81px;
}
.l_i {
  color: #1D1D1D1A!important;
}
.ll_i {
  color: #000;
}
}



.about {
    position: relative;
    overflow: hidden;
    padding-bottom: 50px;
}

.about::before {
    content: "SALES"; 
    position: absolute; 
    color: #1D1D1D08; 
    font-size: 500px;
    font-weight: 700;
    top: 0; 
    left: 0; 
    z-index: -1; 
}

.about__image {
  display: none;
}

.about__us {
    display: flex;
    justify-content: center;
    padding-bottom: 70px;
}

.about__h {
    color: #1D1D1D;
    max-width: 1174px;
    font-weight: 400;
    font-size: 5vw;
    text-align: justify;
}

.about__h span {
    color: #2971C4;
    font-weight: 600;
}


.about__num {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
}

.about__item {
  display: flex;
}

.about__container {
    max-width: 250px;
}

.about__title {
    font-weight: 500;
    font-size: 7vw;
}
.about__p {
    font-size: 20px;
}

@media (max-width: 768px) {
  .about__h {
    font-size: 7vw;
  }
  .about__num {
    justify-items: start;
    gap: 30px;
    grid-template-columns: none;
  }
  .about::before {
    content: ""; 
    display: none;
}
.about__image {
  display: block;
}
  .about__num::before {
    content: "SALES"; 
    position: absolute; 
    color: #1D1D1D08; 
    font-size: 150px;
    font-weight: 700;
    bottom: 20%; 
    right: -170px;
    transform: rotate(90deg);
    z-index: -1; 
}

}


.what {
    background-color: #F8F8F8;
    padding: 80px 0;
    overflow: hidden;
}

.what__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.what__p {
    font-weight: 700;
    font-size: 20px;
    color: #1D1D1D;
}

.what_h {
    font-size: 48px;
    max-width: 510px;
}

.what__btn {
    display: flex;
    gap: 22px;
}

.whates {
    width: 64px;
    height: 64px;
    border-radius: 50%;
}

.what__btn__one {
    background-color: #D9D9D9;
}
.what__btn__two {
    background-color: #2971C4;
}



@media (max-width: 768px) {
  .what__title {
    flex-direction: column;
  }
  }




.slider {
    display: flex;
    gap: 65px;
    margin-top: 60px;
    /* overflow: hidden; */
    width: 100%; 
    transition: transform 0.5s ease-in-out; /* Add smooth transition to the slider container */
 }
 
 .slider__item {
    flex: 0 0 auto;
    /* transition: transform 0.5s ease-in-out; */
    max-width: 500px;
 }

.slider__img {
    width: 500px;
    height: 300px;
    border-radius: 30px;
}

.slider__img:hover {
  filter: brightness(50%) sepia(1) hue-rotate(180deg) saturate(300%);
}

.slider__h {
    margin-top: 20px;
    font-weight: 500;
    font-size: 32px;
}

.slider__p {
    margin-top: 15px;
    font-size: 18px;
    line-height: 28px;
}



.contact {
    display: flex;
    justify-content: space-between;
