    line-height: 27px;
}

#form {
    display: grid;
    justify-items: start;
    margin-top: 35px;
    gap: 35px;
}

#form input {
    color: #fff;
    border-bottom: 1px #fff solid;
    width: 270px;
}
#form input::placeholder {
    color: #fff;
    font-size: 16px;
}

.input__name {
    background: url(./assets/Add-User.svg) no-repeat right top;
    padding-bottom: 10px;
}
.input__phone {
    background: url(./assets/Call.svg) no-repeat right top;
    padding-bottom: 10px;
}


.form__btn {
    padding: 15px 54px;
    color: #1D1D1D;
    background-color: #fff;
    border-radius: 30px;
    font-weight: 600;
    font-size: 16px;
}

.contact__img {
    width: 650px;
    height: 500px;
}

@media (max-width: 768px) {
  .contact {
    flex-direction: column;
    padding: 20px;
  }
  .contact__img {
    padding-top: 40px;
  }
  .contact__img {
    height: 390px;
  }
  }




